import { OnInit, ElementRef, Directive, HostListener, ViewChild } from '@angular/core';
import { BaseService } from '../services/base.service';

@Directive({
    selector: '[playerController]'
})    

export class PlayerControllerDirective implements OnInit{

    

    constructor(private elementRef: ElementRef,private baseService:BaseService) {

    }
    
    ngOnInit(){


    
    }
    @HostListener('timeupdate') fun(event:Event){
        this.baseService.seekbarData.next({"c":this.elementRef.nativeElement.currentTime,"d":this.elementRef.nativeElement.duration});
        // this.seekbar.val=170;
    }
    
}