import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { BaseService } from '../services/base.service';

import {MatSnackBar} from '@angular/material/snack-bar';
import { ThrowStmt } from '@angular/compiler';


@Component({
  selector: 'app-player',
  templateUrl: './player.component.html',
  styleUrls: ['./player.component.scss']
})
export class PlayerComponent implements OnInit, AfterViewInit{
  
  audio=new Audio();
  
  player:any;
  
  cTime:string="00:00:00";
  songDuration:string="00:00:00";
  
  currentSongUrl:string;   
  nextSongUrl:string;
  currentSongName:string;
  nextSongName:string;

  songStatus:string='null';
  playNext:boolean=false;   //check next song to be played or not
  repeatStatus:boolean=false;
  status:boolean;
  queueStatus:boolean=false;

  songUrlQueue:string[]=[];
  songNameQueue:string[]=[];

  @ViewChild('customRange') progressBar:ElementRef;
  
  @ViewChild('repeat') repeatIcon:ElementRef;
  

  constructor(private baseService: BaseService , private snackBar: MatSnackBar) { }

  ngOnInit(): void {


    //current play
    this.baseService.currentSong.subscribe(obj=>{
      
      this.playSong(obj.url,obj.songName);
            
      
    });    

    //update the progress bar and duration span
    this.baseService.seekbarData.subscribe(data=>{
      
        this.cTime=this.sToTime(data.c);
        this.songDuration=this.sToTime(data.d);
        this.progressBar.nativeElement.value = (data.c/data.d );

        if(data.c==data.d && this.repeatStatus){
            this.playSong(this.currentSongUrl,this.currentSongName);
        }
        else if(data.c==data.d && this.playNext){

          // console.log("works");
          this.playSong(this.nextSongUrl,this.nextSongName);
          this.nextSongName='';
          this.playNext=false;
        }
        else if(data.c==data.d && this.queueStatus){

          var index=this.songUrlQueue.indexOf(this.currentSongUrl);
          this.playSong(this.songUrlQueue[index+1],this.songNameQueue[index+1]);
          
          if((index+1) == (this.songUrlQueue.length-1)) this.queueStatus=false;
        }
        else if(data.c==data.d){

          this.songStatus='stoped';
          this.status=false;
        }
  
    });

    //for next song
    this.baseService.nextSong.subscribe(obj=>{
      
          this.nextSongUrl=obj.url;
          this.nextSongName=obj.songName;
          this.playNext=true;

          let snackBarRef = this.snackBar.open("song added","", {
            duration: 1040,
            // here specify the position
            verticalPosition: 'top',
            panelClass: ['next-snackbar'],
          });
    });

    //for song queue
    this.baseService.queueData.subscribe(obj=>{
      this.songUrlQueue.push(obj.url);
      this.songNameQueue.push(obj.songName);
      //console.log(this.songQueue);
      this.queueStatus=true;

      let snackBarRef = this.snackBar.open("song added to queue","", {
        duration: 1040,
        // here specify the position
        verticalPosition: 'top',
        panelClass: ['next-snackbar'],
      });

    });
    

  }

  ngAfterViewInit(): void{
    
    

  }

  playSong(url:string,name?:string){
      this.status=true;
      this.player = <HTMLAudioElement>document.getElementById("player");
      this.player.src=url;
      this.player.load();
      this.player.play();
      this.songStatus='playing';
      this.currentSongUrl=url;
      this.currentSongName=name;
  }

  //for play button
  onPlayClick(){
    this.player.play();
    this.status=true;
    
    this.songStatus='playing';
  }

  //for pause button
  onPauseClick(){
    this.player.pause();
    this.status=false;
    this.songStatus='paused';
  }

  seekbarHandler(){
  }

  sToTime(t:any) {
    return this.padZero(Math.floor((t / (60 * 60)) % 24)) + ":" +
           this.padZero(Math.floor((t / (60)) % 60)) + ":" + 
           this.padZero(Math.floor((t) % 60));
  }
  padZero(v:any) {
    return (v < 10) ? "0" + v : v;
  }

  seek(event:MouseEvent) {
      //  console.log(event);
    var percent = event.offsetX / this.progressBar.nativeElement.offsetWidth;
    this.player.currentTime = Math.floor(percent * this.player.duration);
    this.progressBar.nativeElement.value = percent / 100;
  }
  

  onRepeatClick(){
    this.repeatStatus=!this.repeatStatus;
    
    
  }
}
