import { Injectable, EventEmitter } from '@angular/core';
import { Subject } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class BaseService {

  currentSong = new EventEmitter<{url:string,songName:string}>();

  seekbarData=new Subject<{c:number,d:number}>();
  nextSong=new EventEmitter<{url:string,songName:string}>();
  queueData=new EventEmitter<{url:string,songName:string}>();

  constructor() { }
}
