import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import { BaseService } from '../services/base.service';
import { Song } from '../song';

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss']
})
export class CardComponent implements OnInit {

  @Input()
  imgUrl:string;

  @Input()
  songName:string;
  @Input()
  songPath:string;
  @Input()
  count:number;
  @Input()
  artist:string;
  @Input()
  releaseDate:string;

  @ViewChild('playNext') playNextIcon:ElementRef;

  constructor(private baseService: BaseService ) { }

  ngOnInit(): void {
    // console.log(this.song.imgUrl);
  }

  imgClick(){
    this.baseService.currentSong.emit({"url":this.songPath,"songName":this.songName});
  }

  playNext(){
    
    this.baseService.nextSong.emit({"url":this.songPath,"songName":this.songName});
  }

  onQueueClick(){
    this.baseService.queueData.emit({"url":this.songPath,"songName":this.songName});
  }
}
